body {
  background-color: rgb(38, 47, 64);
}

.top-right {
  color: whitesmoke;
  font-size: 14px;
  position: absolute;
  top: 20px;
  right: 40px;
}

.title {
  font-family: Courier;
}

.header {
  background-color: black;
  z-index: -1;
  height: 400px;
}

.header::after {
  content: "";
  background: url("./components/images/header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.4;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1;
  height: 400px;
}

.box {
  background-color: rgba(128, 128, 128, 0.7);
  border-radius: 10px;
  padding: 20px;
  z-index: 1;
}

.font-big {
  font-size: 50px;
}

.bg-whitesmoke {
  padding-top: 150px;
  padding-bottom: 150px;
  background-color: whitesmoke;
}

.bg-nav {
  background-color: rgb(38, 47, 64);
}

.font {
  font-family: Brush Script MT;
}

.onScroll {
  opacity: 0;
  transition: opacity 0.2s linear;
}

.onScroll.show {
  opacity: 1;
  transition: opacity 0.4s ease-out;
}

.homeBtn {
  position: fixed;
  bottom: 40px;
  right: 40px;
  font-size: 18px;
  padding: 18px 20px 18px 20px;
  background-color: rgb(234, 105, 70);
}

.bg-orange {
  background-color: rgba(234, 105, 70, 0.9);
}

.carousel-control-next,
.carousel-control-prev {
  filter: invert(100%) !important;
}

hr {
  border: 0;
  height: 3px !important;
  background: linear-gradient(
    to right,
    rgba(0, 0, 0, 0),
    rgba(255, 69, 0, 1),
    rgba(0, 0, 0, 0)
  ) !important;
}

.zoom {
  transition: transform 0.2s;
}

.zoom:hover {
  transform: scale(1.025);
  right: 0;
}

img.item-image {
  width: 95%;
  height: 300px;
}

#slider10,
#slider6 {
  width: 250px;
  height: 400px;
}

#slider9 {
  width: 500px;
  height: 400px;
}

.closeBtn {
  overflow: hidden;
  cursor: pointer;
  transition-duration: 0.8s;
  transition-property: transform;
}
.closeBtn:hover {
  transform: rotate(360deg);
  -webkit-transform: rotate(360deg);
}

.i-skills {
  width: 150px;
  height: 150px;
  margin-top: 20px;
  margin-bottom: 20px;
  border: 2px solid whitesmoke;
  border-radius: 20px;
}

.a-skills {
  margin: 20px;
}

.a-skills:hover {
  background-color: transparent;
  text-decoration: none;
}
